function showUpdateOptions() {
    const medicineId = document.getElementById('medicineId').value;
    if (medicineId) {
        document.getElementById('updateOptions').style.display = 'block';
    } else {
        alert('Please enter a valid Medicine ID');
    }
}

function showUpdateDialog(field) {
    let dialogContent = '';
    switch (field) {
        case 'name':
            dialogContent = `
                <h3>Update Medicine Name</h3>
                <label for="oldName">Current Name:</label>
                <input type="text" id="oldName" name="oldName">
                <label for="newName">New Name:</label>
                <input type="text" id="newName" name="newName">
                <button onclick="confirmUpdate('name')">Confirm</button>
            `;
            break;
        case 'price':
            dialogContent = `
                <h3>Update Medicine Price</h3>
                <label for="oldPrice">Current Price:</label>
                <input type="number" id="oldPrice" name="oldPrice" step="0.01">
                <label for="newPrice">New Price:</label>
                <input type="number" id="newPrice" name="newPrice" step="0.01">
                <button onclick="confirmUpdate('price')">Confirm</button>
            `;
            break;
        case 'image':
            dialogContent = `
                <h3>Update Medicine Image</h3>
                <label for="oldImage">Current Image:</label>
                <input type="file" id="oldImage" name="oldImage" disabled>
                <label for="newImage">New Image:</label>
                <input type="file" id="newImage" name="newImage" accept="image/*">
                <button onclick="confirmUpdate('image')">Confirm</button>
            `;
            break;
        case 'description':
            dialogContent = `
                <h3>Update Medicine Description</h3>
                <label for="oldDescription">Current Description:</label>
                <textarea id="oldDescription" name="oldDescription" disabled></textarea>
                <label for="newDescription">New Description:</label>
                <textarea id="newDescription" name="newDescription"></textarea>
                <button onclick="confirmUpdate('description')">Confirm</button>
            `;
            break;
        default:
            return;
    }
    document.getElementById('dialogContent').innerHTML = dialogContent;
    document.getElementById('updateDialog').style.display = 'block';
}

function closeDialog() {
    document.getElementById('updateDialog').style.display = 'none';
}

function confirmUpdate(field) {
    switch (field) {
        case 'name':
            const oldName = document.getElementById('oldName').value;
            const newName = document.getElementById('newName').value;
            console.log(`Updating Name from ${oldName} to ${newName}`);
            break;
        case 'price':
            const oldPrice = document.getElementById('oldPrice').value;
            const newPrice = document.getElementById('newPrice').value;
            console.log(`Updating Price from ${oldPrice} to ${newPrice}`);
            break;
        case 'image':
            const newImage = document.getElementById('newImage').files[0];
            console.log(`Updating Image to ${newImage.name}`);
            break;
        case 'description':
            const newDescription = document.getElementById('newDescription').value;
            console.log(`Updating Description to ${newDescription}`);
            break;
    }
    closeDialog();
    alert('Medicine updated successfully!');
}





